@-webkit-keyframes appear {
  from { -webkit-transform: scaleY(0); transform: scaleY(0); }
  to   { -webkit-transform: scaleY(100%); transform: scaleY(100%); }
}

@keyframes appear {
  from { -webkit-transform: scaleY(0); transform: scaleY(0); }
  to   { -webkit-transform: scaleY(100%); transform: scaleY(100%); }
}

@-webkit-keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.card {
  position: relative;
  width: 30%;
  height: 400px;
  padding: 20px;
  margin: 10px;
  border: 2px solid #494949;
  border-radius: 5px;
  color: #FFF;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
  overflow: hidden;
}

.card-animation {
  -webkit-animation: appear 0.2s linear;
          animation: appear 0.2s linear;
}

.card-animation > * {
  -webkit-animation: fadeIn 1.2s linear;
          animation: fadeIn 1.2s linear;
  opacity: 1;
}

.card-animation .bg-emblem {
  -webkit-animation: none;
          animation: none;
  z-index: 0;
}

.card-animation .bg-vehicle {
  -webkit-animation: none;
          animation: none;
  z-index: 0;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 20rem 20rem;
  background-position: center 0;
  opacity: 0;
}

.bg-stars {
  background-position: center 0;
  opacity: 1;
}

.bg-emblem {
  position: absolute;
  top: 5%;
  left: 25%;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
  opacity: 0.2;
}

.bg-vehicle {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 800px;
  background-position: 40% 50%;
  opacity: 0.3;
}

.card-selected {
  border: 3px solid #FEDA4A;
}

.card-title-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 40px 0 0;
}

.card-title {
  text-transform: uppercase;
  z-index: 2;
}

.card-info-list {
  width: 50%;
  padding: 0;
  list-style: none;
  z-index: 2;
}

.info {
  margin: 0 30px 20px 0;
}

.info-label {
  padding: 0 0 5px;
  margin: 0 0 5px;
  border-bottom: 1px solid #FFF;
  font-size: 15px;
  font-weight: 300;
}

.info-value {
  font-size: 12px;
}

.info-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 75%;
  margin: 20px 0;
  overflow: scroll;
}

.card-selected .card-favorite-button {
  opacity: 1;
}

.card-favorite-button {
  position: absolute;
  top: 2%;
  left: 5%;
  width: 90%;
  padding: 5px 10px;
  margin: 0 auto;
  background-color: transparent;
  border: 3px solid #FFF;
  border-radius: 10px;
  color: #FFF;
  font-family: Starjedi, Ubuntu, sans-serif;
  letter-spacing: 2px;
  opacity: 0.6;
  z-index: 3;
}

.card-favorite-button:hover {
  opacity: 1;
}

.card-favorite-button:focus {
  outline: none;
}

.residents {
  overflow: scroll;
}



