.scroller {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 250px;
  -webkit-perspective: 350px;
          perspective: 350px;
  color: #feda4a;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 250%;
  font-weight: 600;
  letter-spacing: 6px;
  line-height: 150%;
  text-align: justify;
  overflow: hidden;
  background-image: url('../../utils/stars.svg');
}

.scroller:after {
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 100%;
  content: "";
  background: -webkit-gradient(linear,
     left top, left bottom,
     color-stop(5%, rgba(0,0,0, 1)),
     color-stop(80%, rgba(255,255,255, 0))
  );
  background: -webkit-linear-gradient(top,
     rgba(0,0,0, 1) 5%,
     rgba(255,255,255, 0) 80%
  );
  background: -o-linear-gradient(top,
     rgba(0,0,0, 1) 5%,
     rgba(255,255,255, 0) 80%
  );
  background: linear-gradient(to bottom,
     rgba(0,0,0, 1) 5%,
     rgba(255,255,255, 0) 80%
  );
}

.crawl {
  width: 80%;
  position: relative;
  margin-top: 300px;
  -webkit-transform-origin: 50% 100%;
      -ms-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-animation: crawl 120s linear;
          animation: crawl 120s linear;
}

.title-container {
  text-align: center;
  margin-bottom: 30px;
}

.film-title {
  margin-bottom: 15px;
}

.film-date {
  margin-top: 25px;
  text-align: center;
}

.star-wars {
  position: absolute;
  z-index: 5;
  text-align: center;
  margin-top: 75px;
  font-size: 105px;
  font-weight: 600;
  font-family: Starjedi, Ubuntu, sans-serif;
  line-height: 100%;
  letter-spacing: 6px;
  color: #000;
  text-stroke: 1px black;
  text-shadow:
     4px 4px 0 #feda4a,
   -4px -4px 0 #feda4a,
    4px -4px 0 #feda4a,
    -4px 4px 0 #feda4a,
     4px 4px 0 #feda4a;
  -webkit-animation: star-wars 50s linear;
          animation: star-wars 50s linear;
}

@-webkit-keyframes crawl {
  0% {
    top: 0;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0);
  }
  100% {
    top: -5000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

@keyframes crawl {
  0% {
    top: 0;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0);
  }
  100% {
    top: -5000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

@-webkit-keyframes star-wars {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  75% {
    opacity: 0
  }
  100% {
    opacity: 1;
  }
}

@keyframes star-wars {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  75% {
    opacity: 0
  }
  100% {
    opacity: 1;
  }
}



