@-webkit-keyframes ring1 {
  0%   { -webkit-transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg); transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg) }
  50%  { -webkit-transform: rotateY(20deg) rotateX(-1080deg) rotateZ(-5deg); transform: rotateY(20deg) rotateX(-1080deg) rotateZ(-5deg) }
  100%  { -webkit-transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg); transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg) }
}

@keyframes ring1 {
  0%   { -webkit-transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg); transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg) }
  50%  { -webkit-transform: rotateY(20deg) rotateX(-1080deg) rotateZ(-5deg); transform: rotateY(20deg) rotateX(-1080deg) rotateZ(-5deg) }
  100%  { -webkit-transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg); transform: rotateY(-20deg) rotateX(10deg) rotateZ(5deg) }
}

@-webkit-keyframes ring2 {
  0%   { -webkit-transform: rotateY(50deg) rotateX(70deg) rotateZ(-5deg); transform: rotateY(50deg) rotateX(70deg) rotateZ(-5deg) }
  50% { -webkit-transform: rotateY(-50deg) rotateX(1080deg) rotateZ(5deg); transform: rotateY(-50deg) rotateX(1080deg) rotateZ(5deg)}
  100% { -webkit-transform: rotateY(-50deg) rotateX(70deg) rotateZ(-5deg); transform: rotateY(-50deg) rotateX(70deg) rotateZ(-5deg)}
}

@keyframes ring2 {
  0%   { -webkit-transform: rotateY(50deg) rotateX(70deg) rotateZ(-5deg); transform: rotateY(50deg) rotateX(70deg) rotateZ(-5deg) }
  50% { -webkit-transform: rotateY(-50deg) rotateX(1080deg) rotateZ(5deg); transform: rotateY(-50deg) rotateX(1080deg) rotateZ(5deg)}
  100% { -webkit-transform: rotateY(-50deg) rotateX(70deg) rotateZ(-5deg); transform: rotateY(-50deg) rotateX(70deg) rotateZ(-5deg)}
}

.planet-wraper {
  position: absolute;
  top: 35%;
  left: 32%;
  width: 170px;
  height: 170px;
  border-radius: 50%;
  -webkit-box-shadow: inset 0 -5px 10px rgba(22, 48, 64, 0.5), 0 0 80px rgba(192, 212, 212, 0.5);
          box-shadow: inset 0 -5px 10px rgba(22, 48, 64, 0.5), 0 0 80px rgba(192, 212, 212, 0.5);
  opacity: 0.6;
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}

.card-selected .planet-wraper::before, .planet-wraper::after {
  content: '';
  display: block;
  position: absolute;
  border-radius: 50%;
  -webkit-box-shadow: 0 0rem 1rem rgba(170, 194, 194, 0.9), inset 0 -0.5rem 2rem rgba(170, 194, 194, 0.9);
          box-shadow: 0 0rem 1rem rgba(170, 194, 194, 0.9), inset 0 -0.5rem 2rem rgba(170, 194, 194, 0.9);
}

.card-selected .planet-wraper::before {
  top: -50%;
  left: -50%;
  width: 20.5rem;
  height: 20.5rem;
  -webkit-animation: ring2 12s infinite linear;
          animation: ring2 12s infinite linear;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
  -webkit-transform: rotateY(50deg) rotateX(70deg) rotateZ(5deg);
          transform: rotateY(50deg) rotateX(70deg) rotateZ(5deg);
}

.card-selected .planet-wraper::after {
  top: -55%;
  left: -55%;
  width: 20.5rem;
  height: 20.5rem;
  -webkit-animation: ring1 10s infinite linear;
          animation: ring1 10s infinite linear;
  -webkit-transform: rotateY(-20deg) rotateX(70deg) rotateZ(-5deg);
          transform: rotateY(-20deg) rotateX(70deg) rotateZ(-5deg);
