.favorite-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 260px;
  min-width: 260px;
  padding: 10px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: 5% 50%;
  background-size: 25%;
  border: 3px solid #FFF;
  border-radius: 20px;
  color: #FFF;
  cursor: pointer;
  font-family: Starjedi, sans-serif;
  letter-spacing: 3px;
  opacity: 0.7;
}

.in-favorites {
  opacity: 1;
}

.favorite-button:hover {
  opacity: 1;
}

.favorite-button:focus {
  outline: none;
}

.favorite-button-label {
  width: 100%;
  text-align: right;
}



